import React from "react";
import './estilo.css'
import edit2 from '../imagens/edit.svg'
import select_edit from '../imagens/select_edit.svg'
import select_dowload from '../imagens/select_download.svg'
import select_delete from '../imagens/select_delete.svg'

import mais from '../imagens/mais2.svg'
import menos from '../imagens/delete(1).svg'
import baixar from '../imagens/download.svg'
import upload from '../imagens/upload.svg'
import reset from '../imagens/reset2.svg'
import { createRef } from "react";

export default class File extends React.Component {


    constructor(props) {
        super(props);
        this.state = {
            nome: '',
            nós: [],
            id: this.props.id,
            texto: "on",
        }
        // this.upload = this.upload.bind(this)
        // this.novo_projeto = this.novo_projeto.bind(this)
        this.corpo = React.createRef()
        this.texto = React.createRef()
        this.locação = React.createRef();
        this.edit = this.edit.bind(this)
        this.remove = this.remove.bind(this)
        this.download = this.download.bind(this)
        this.seleciona = this.seleciona.bind(this)

        this.click = this.click.bind(this)
    }
    seleciona(x) {
        let list = document.getElementById('ul')
        this.setState({ nome: this.props.nome })
        let corpo = this.corpo.current
        let projeto = this.props.projeto
        let id = this.state.id
        let texto = this.texto.current

        for (let i = 0; i < list.children.length; i++) {
            const element = list.children[i];
            element.children[0].children[0].style = 'background-color:white'
            element.children[0].children[1].children[0].src = edit2
            element.children[0].children[2].children[0].src = baixar
            element.children[0].children[3].children[0].src = menos
            element.children[0].style.borderBottom = '  1px solid rgba(210,210,210,2)'
        }   

       if(projeto!=undefined){ if(projeto.id==id && texto!=undefined){
            texto.style.color = '#F99245'
            corpo.style.borderBottom = ' 1px solid #F99245'
        }}
        



    }
    click() {
        let Deus = this.props.Deus
        let id = this.state.id
        if (id != null) {
            Deus.carregar_projeto(this.state.id)
            window.setTimeout(() => {
                this.seleciona()
            }, 0);
           
          
        }

    }
    download() {
        let textDoc = document.createElement('a');
        let mmd = [];
        let nós = this.state.nós
        let nome = this.state.nome

        console.log(this.props.projeto)
        mmd = JSON.stringify(this.props.projeto);
        console.log(mmd)
        textDoc.href = 'data:attachment/text,' + encodeURI(mmd);
        textDoc.target = '_blank';
        textDoc.download = nome + '.mmd';
        textDoc.click();
    }
    edit(params) {
        let nome = this.state.nome
        let texto = this.texto.current
        nome = window.prompt('Novo nome')
        texto.innerText = nome
        let projetos = this.props.projetos
        let id = this.state.id
        let projeto = projetos[id]
        projeto.nome = nome
        console.log(projetos)


    }
    remove(params) {
        console.log('antes')
        let Deus = this.props.Deus
        this.setState({id:null})

        Deus.exclui_projeto(this.state.id)


    }

    componentDidMount() {
        this.seleciona(true)
      

    }
    render() {
        // let x = this.novo_projeto
        let nome = this.state.nome
        return (
            <li ref={this.locação} class="files_li">
                <div onClick={this.click} ref={this.corpo} className="files_div_list">
                    <span ref={this.texto} className="files_div_list_texto">{nome}</span>
                    <div onClick={this.edit} className="files_div_list_btn">   <img className="files_div_list_img" src={baixar}></img></div>
                    <div onClick={this.download} className="files_div_list_btn"><img className="files_div_list_img" src={menos}></img></div>
                    <div onClick={this.remove} className="files_div_list_btn" > <img className="files_div_list_img" src={edit2}></img></div>
                </div>
            </li>
        )

    }
}
