import React from "react";
import './estilo.css'
import edit2 from '../imagens/edit.svg'
import select_edit from '../imagens/select_edit.svg'
import select_dowload from '../imagens/select_download.svg'
import select_delete from '../imagens/select_delete.svg'

import mais from '../imagens/mais2.svg'
import menos from '../imagens/delete(1).svg'
import baixar from '../imagens/download.svg'
import upload from '../imagens/upload.svg'
import reset from '../imagens/reset2.svg'
import File from "./arquivos";
export default class Files extends React.Component{

    
    constructor(props){
        super(props);
        this.state={
            projetos:[],    
            texto:"on",
        }
        this.upload=this.upload.bind(this)
        this.novo_projeto=this.novo_projeto.bind(this)
    }
  
        
    clear(){
        this.props.Deus.resetar_projetos()

        this.atualizar([])
        
    }
    upload(){
        console.log("s")
           console.log(this.props["cria_novoNó"])
           let cria_projeto = this.novo_projeto
           
            
            let x = document.createElement('input')
            x.type="file"
            x.click()
            x.onchange = function(event) {
                let cria_nó =this.props["cria_novoNó"]
                var fr=new FileReader(); 
                let arquivo = []
                

                fr.onload=function(){ 
                    let resultado = fr.result
                    // arquivo.push(resultado.replace(/(\r\n|\n|\r)/gm, "").split('|'))
                    let obj = JSON.parse(resultado)
                    let id = this.props.Deus.upar_projeto(obj.nome,obj.nos)
                    
                    console.log(obj)
                    this.props.Deus.carregar_projeto(id)    
                   

                }.bind(this)
                fr.readAsText(event.target.files[0]); 
           
            }.bind(this)

            

        }
        
         filtrar = function() {
             //boa sorte tentando entender isso
            console.log('sa')
            let filter, ul, li, a, i, txtValue;
    
            let input = this.refs.input
            filter = input.value.toUpperCase();
            ul =this.refs.list
            li = ul.getElementsByTagName("li")
            console.log(li)
            for (i = 0; i < li.length; i++) {
                
                let a = li[i].children[0].getElementsByTagName('span')[0]
                txtValue = a.textContent || a.innerText;
                if (txtValue.toUpperCase().indexOf(filter) > -1) {
                    li[i].style.display = "";
                } else {
                    li[i].style.display = "none";
                }
            } 
           
              
            
        }  
    

    
    novo_projeto=()=>{
        let nome = window.prompt('novo s:')
        let Deus = this.props.Deus
        let id = Deus.cria_projeto(nome)

        Deus.carregar_projeto(id)
        this.atualizar(this.props.projetos)
        
    }
    atualizar=function(_projetos){
        this.setState({projetos:[]})
        window.setTimeout(() => {
            this.setState({projetos:_projetos})
            }, 10);

        console.log('atualizei' )
    }.bind(this)
    componentDidMount(){
        let projetos=this.props.projetos
        this.setState({projetos:projetos})

        
            this.props.amarração(this.atualizar)
     

        
    }
    componentDidUpdate(){
        let projetos=this.props.projetos
        // console.log('update projetos')
        // console.log(this.state.projetos)
    }

    render(){
        // console.log(this.props.projeto_selecionado)        
        return(
            <div class="files_main">
                <div class='files_div_h1'>
                    <span class='files_h1'>projetos:</span>
                </div>
                <div  class="files_explorer">
                    <ul  id='ul' ref='list' class='files_list'> 
                    { this.state.projetos.map( (projeto)=> {
                        
                        return (
                            
                        <File
                            Deus={this.props.Deus}
                            nome={projeto['nome']}
                            projetos={this.state.projetos}
                            projeto={this.props.projeto_selecionado}
                            nos={projeto['nos']}
                            id={projeto['id']}> 
                        </File>
                        
                        )

                    })}
                        {/* <File Deus ={this.props.Deus}nome='teste'></File> */}
                    </ul>
                </div>

                <div class='files_tollbar'>
                        {this.props.test}
                        <div class='files_botão'  onClick={()=>this.novo_projeto()}   style={{right: 0 + 'px'}}>
                            
                            <img class='files_img' src={mais}></img>
                        </div>
                        <div class='files_botão'  onClick={this.upload.bind(this)} style={{right: 40 + 'px'}}>
                            <img class='files_img' src={upload}></img>

                        </div>
                        <div class='files_botão'  onClick={this.clear.bind(this)} style={{right: 80 + 'px'}}>
                             <img class='files_img' src={reset}></img>

                        </div>
                        <input ref='input' onKeyUp={()=>this.filtrar()}  class="files_searchBar">
                        </input>
                </div>
            </div>
        )
        
    }
}