import React from "react";
import ReactDOM from "react-dom";
import './estilo.css'
import No from '../toolbar/nó'

export default class Workbanch extends React.Component {
    constructor(props) {
        super(props);

        this.state = {

            screenx: window.innerWidth,
            screeny: window.innerHeight,
            down: false,
            pos: [],
            nós: '',
            nos: []



        }
        this.handleMouseMove = this.handleMouseMove.bind(this)
        this.handleMouseUp = this.handleMouseUp.bind(this)

        this.atualiza = this.atualiza.bind(this)

    }

    pos = 0
    componentDidMount() {
      
       
        

        // window.onresize = function () {
        //     let delta = (this.state.screeny * 6) / 100
        //     this.setState({
        //         screenx: window.innerWidth,
        //         screeny: window.innerHeight,
        //     })
        //     this.refs.workbanch.scrollTo(0, 0);
        // }.bind(this)
        // this.setState({ nós: this.props.projeto_selecionado.nós })
    }

    desenhaConexao(element1, element2) {
        const canvas = this.refs.canvas

        let ctx = canvas.getContext("2d")

        ctx.beginPath();
        ctx.strokeStyle = "#4169e1";
        console.log('elemetnos')
        console.log(element1)
        console.log(element2)

        let posx1 = parseInt(element1.style.left)
        let posy1 = parseInt(element1.style.top)
        let posx2 = parseInt(element2.style.left)
        let posy2 = parseInt(element2.style.top)

        let deltax1 = element1.offsetWidth / 2
        let deltay1 = element1.offsetHeight / 2

        let deltax2 = element2.offsetWidth / 2
        let deltay2 = element2.offsetHeight / 2

        let x1 = parseInt(posx1 + deltax1)
        let y1 = parseInt(posy1 + deltay1)

        let x2 = parseInt(posx2 + deltax2)
        let y2 = parseInt(posy2 + deltay2)

        let pos1 = [x1, y1]

        let pos2 = [x2, y2]
        // console.log(pos2)
        // console.log(pos1)

        let distanciaX = pos1[0] + pos2[0]
        let distanciaY = pos1[1] + pos2[1]

        ctx.moveTo(pos1[0], pos1[1])
        ctx.lineTo(pos1[0], distanciaY * 0.5)
        ctx.stroke();

        ctx.moveTo(pos1[0], distanciaY * 0.5)
        ctx.lineTo(pos2[0], distanciaY * 0.5)
        ctx.stroke();

        ctx.moveTo(pos1[0], distanciaY * 0.5)
        ctx.lineTo(pos2[0], posy2[1])
        ctx.stroke();



    }
    handleMouseMove(e) {
        const canvas = this.refs.canvas

        let projeto = this.props.projeto_selecionado


        if (this.props.nohtml_selecionado != '') {
            let delta = (this.state.screeny * 6) / 100
            // console.log('props ')
            let noid = this.props.no_selecionado
            let no = document.getElementById(noid)
      
            let posiçãoMouseX = parseInt(e.nativeEvent.clientX) - 50
            let posiçãoMouseY = e.nativeEvent.clientY
            let tamanhoNoW = no.offsetWidth / 2
            let tamanhoNoH = no.offsetHeight / 2
            let posiçãoFinalMouseY = posiçãoMouseY - delta - tamanhoNoH
            let posiçãoFinalMouseX = posiçãoMouseX - tamanhoNoW

            // console.log('no')
            // console.log(no)
            no.style.left = posiçãoFinalMouseX + 'px'
            no.style.top = posiçãoFinalMouseY + 'px'


            let ctx = canvas.getContext("2d")
            ctx.clearRect(0, 0, canvas.width, canvas.height)
            ctx.beginPath();
            this.pos = { x: posiçãoFinalMouseX, y: posiçãoFinalMouseY }


            let nos = projeto.nos
            nos.forEach(no => {
                let noAid = no['id']
                let noAconexoes = no['conexoes']
                let noA = document.getElementById(noAid)

                noAconexoes.forEach(noBid => {
                    let noB = document.getElementById(noBid)

                    this.props.desenhador(noA, noB)
                });

            });



        } else { }

    }


    handleMouseUp() {
        let projeto = this.props.projeto_selecionado

        if (typeof projeto !== 'undefined') {
            projeto.nos.forEach(_no => {
                if (_no.id == this.props.no_selecionado) {
                    _no['pos'] = {
                        x: this.pos.x,
                        y: this.pos.y
                    }
                    console.log(projeto)
                }
            });
        }
    }


    atualiza(_nos) {
        let arr = this.state.nos

        this.setState({ nos: [] })

        window.setTimeout(() => {
            this.setState({ nos: _nos })

        }, 1)

        console.log('reodei aqui')
        console.log(_nos)


    }
    componentDidMount() {
        console.log('nos')
        console.log(this.state.nos)
        this.props.amarração(this.atualiza)

         const canvas = this.refs.canvas
        const ctx = canvas.getContext("2d")
        let delta = this.state.screeny - (this.state.screeny * 6) / 100

        canvas.height = delta
        
        this.refs.workbanch.scrollTo(0, 0);
        window.onresize = function () {
            let delta = (this.state.screeny * 6) / 100
            this.setState({
                screenx: window.innerWidth,
                screeny: window.innerHeight,
            })
            this.refs.workbanch.scrollTo(0, 0);
        }.bind(this)
        // this.setState({ nós: this.props.projeto_selecionado.nós })

    }

    render() {
        let delta = this.state.screeny - (this.state.screeny * 6) / 100

        return (

            <div ref="workbanch" id='workbanch_main' class='workbanch_main' style={{ height: delta, width: this.state.screenx - 372 + 'px' }} className="workbanch_main" onMouseUp={this.handleMouseUp} onMouseDown={this.down} onMouseMove={this.handleMouseMove}>


                {this.state.nos.map((no) => {


                    return (<No
                        seleciona_no={this.props.seleciona_no}

                        projeto_selecionado={this.props.projeto_selecionado}
                        atualiza_toolbarmode={this.props.atualiza_toolbarmode}
                        Deus={this.props.Deus}
                        toolbarmode={this.props.toolbarmode}
                        id={no['id']}
                        desenhador={this.props.desenhador}
                        id_proj={this.props.id_proj}
                        texto={no['texto']}>

                    </No>)

                })}
                <canvas ref="canvas" id='canvas' class='workbanch_canvas' height={delta + 'px'} width={this.state.screenx - 372 + 'px'}></canvas>

            </div>
        )


    }

}